.pulse {

    /* display: block; */
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background: #0063FF;
    cursor: pointer;
    box-shadow: 0 0 0 rgba(2, 30, 90, 0.4);
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
    /* padding-top: 20px; */
}

.pulse:hover {
    -webkit-animation: none;
            animation: none;
}

@-webkit-keyframes pulse {
    0% {
        -webkit-box-shadow: 0 0 0 0 rgba(2, 30, 90, 0.4);
    }

    70% {
        -webkit-box-shadow: 0 0 0 10px rgba(2, 30, 90, 0);
    }

    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(2, 30, 90, 0);
    }
}

@keyframes pulse {
    0% {
        box-shadow: 0 0 0 0 rgba(2, 30, 90, 0.4);
    }

    70% {
        box-shadow: 0 0 0 10px rgba(2, 30, 90, 0);
    }

    100% {
        box-shadow: 0 0 0 0 rgba(2, 30, 90, 0);
    }
}
