.handimagebox {
    width: 100px;
    height: 100px;
    border-radius: 2px;
    margin: 0px 10px 5px 0px;
    display: inline-block;
    z-index: 0;
    transition: transform .2s;
    border-bottom: 6px solid #fff;
}

.handimageselected {
    border-bottom: 6px solid #0063FF;
}

.flipimagecheckbox {
    float: right;
}

.handimagebox:hover {
    cursor: pointer;
    border-bottom: 6px solid #0063FF;
    /* transform: scale(1.4); */
    z-index: 1000;
}

.videoboxes {
    z-index: -100;
    min-width: 920px;
}

.videobox {
    /* display: none; */
    border-radius: 2px;
    margin: 0px 6px 0px 0px;
    object-fit: cover;
}

.canvasbox {
    border-radius: 3px;
    /* margin-right: px; */
    /* width: 450px; */
    /* height: 338px; */
    border-bottom: 3px solid #0063FF;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.2), 0 4px 10px 0 rgba(0, 0, 0, 0.19);
}

.videoflip {
    transform: rotateY(180deg);
    -webkit-transform: rotateY(180deg);
    /* Safari and Chrome */
    -moz-transform: rotateY(180deg);
    /* Firefox */
}

.slidecontainer {
    /* Width of the outside container */
    margin-bottom: 15px;
}

.confidencethreshold {
    margin: 0px 0px 0px 10px;
    font-weight: bold;
}

.loadingtitle {
    position: absolute;
    padding: 8px 0px 0px 5px;
    font-weight: bold;
}

/* The slider itself */
.slider {
    -webkit-appearance: none;
    /* Override default CSS styles */
    appearance: none;
    width: 100%;
    /* Full-width */
    height: 8px;
    /* Specified height */
    background: #d3d3d3;
    /* Grey background */
    outline: none;
    /* Remove outline */
    opacity: 0.7;
    /* Set transparency (for mouse-over effects on hover) */
    -webkit-transition: .2s;
    /* 0.2 seconds transition on hover */
    transition: opacity .2s;
}

/* Mouse-over effects */
.slider:hover {
    opacity: 1;
    /* Fully shown on mouse-over */
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    /* Override default look */
    appearance: none;
    width: 20px;
    /* Set a specific slider handle width */
    height: 25px;
    /* Slider handle height */
    background: #0063FF;
    border-radius: 4px;
    /* Green background */
    cursor: pointer;
    /* Cursor on hover */
}

.slider::-moz-range-thumb {
    width: 25px;
    /* Set a specific slider handle width */
    height: 25px;
    /* Slider handle height */
    background: #0063FF;
    /* Green background */
    cursor: pointer;
    /* Cursor on hover */
}

/* Pattern styles */
.containerflex {
    display: flex;
}

.left-half {
    flex: 1;
}

.clickable {
    position: relative;
    display: inline-block;
    width: 100%;
}

.right-half {
    width: 450;
}

/* Game canvas sytl */
.gamecontainer {
    width: 500px;
    height: 500px;
}

.gamecanvas {
    background-color: rgb(34, 34, 34);
    display: block;
    /* height: 300px; */
    /* max-height: 300px; */
}

.videocanvasoutput {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 150px;
    height: 120px;
    border-bottom: 4px solid #0063FF;
    border-radius: 3px;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.2), 0 4px 10px 0 rgba(0, 0, 0, 0.19);
}

.doodlecolor {
    height: 60px;
    background: #0063FF;
    transition: background-color 0.5s ease;
}

.saveddoodle {
    height: 50px;
    width: 50px;
    background: #0063FF;
}

.doodleimage {
    height: 100px;
    width: 100px;
    border-bottom: #0063FF 3px solid;
    border-top: #0063FF 3px solid;
    background: rgba(211, 211, 211, 0.2);
}

.usageexamples {
    font-size: 0.9em;
    line-height: 1.2em;
}

.infordisplay {
    position: absolute;
    margin: 2vw;
    top: 0.0vw;
    color: #fff;
}

.floatleft {
    float: left;
}

.clear {
    clear: both;
}

.icon {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    width: 3.2vw;
    margin: 0vw;
}

.scorebox {
    height: 100%;
    border-bottom: 0.1vw solid #ff8800;
    padding: 1vw;
    font-size: 1.3vw;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: #fff;
    margin: 0vw 0vw 0vw 0vw;
}

.inscore {
    display: inline-block;
}

.scorevalue {
    font-size: 1.5vw;
    font-weight: bold;
}

.scoreadded {
    position: absolute;
    font-size: 2vw;
    font-weight: bold;
    padding: 0vw 0vw 0vw 1vw
}

.scoreanimate {
    transform: scale(3);
    transition: all 2s;
}

.soundofftext {
    padding: 0.1vw 1vw 0vw 0.5vw;
    height: 100%;
    font-size: 1.2vw;
}

.pausetext {
    padding: 1vw 0vw 0vw 0vw;
    font-size: 1vw;
}